.headerOuter {
    min-height: 15vh;
    max-height: 100px;
    box-shadow: 0px 0px 8px 2px #000000;
    display: inline-flex;
    width: 100%;
    justify-content: center;
    flex-direction: column;
    /* transition: ease-in-out 3s; */
    /* animation-name: fade-in;
    animation-duration: 3s; */
}

.headerOuter.active {
    position: sticky;
    top: 0;
    /* background-color: red; */
    background-color: white;
    /* transition: ease-in-out 3s; */
    animation-name: fade-in;
    animation-duration: 2s;
    z-index: 1000;
}

@keyframes fade-in {
    from{opacity: 0;}
    to{opacity: 1;}
}
@-webkit-keyframes fade-in{
    from{opacity: 0;}
    to{opacity: 1;}
}

.link {
    width: 20%;
    margin: auto;
}

.bottomDiv {
    width: 100%;
    margin-top: 1%;
    display: inline-flex;
}

.rightDiv {
    width: 15%;
    margin: 0 2%;
    text-align: end;
}

